    height: 2.2em;
    margin-bottom: 0;
    overflow: hidden;
    /* Line clamp for webkit */
    /* Line clamp for non-webkit */ }
    .webkit .card_title {
      /* If this is webkit browser, use line clamp */
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical; }
    .no-webkit .card_title {
      /* If this is webkit browser, use line clamp */
      position: relative; }
      .no-webkit .card_title:after {
        background: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff 50%);
        bottom: 0;
