/* Specific styles for cards to look like a stack of cards */
.card-stack {
  overflow: visible;
  /* Styles for pseudo-elements that give a card some depth */
  /* Different height for before element */
  /* On hover, increase the height of the pseudo-elements to generate expansion */ }
  .card-stack:after, .card-stack:before {
    background: #fff;
    border-radius: 0 0 0.25em 0.25em;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.12) inset, 0 2px 0 rgba(0, 0, 0, 0.08);
    content: "";
    display: block;
    height: 5px;
    left: 10px;
    right: 10px;
    position: absolute;
    top: 100%;
    transition: 0.125s ease-in-out; }
  .card-stack:before {
    height: 10px;
    left: 20px;
    right: 20px; }
  .card-stack:hover:before {
    height: 20px; }
  .card-stack:hover:after {
    height: 10px; }

